
.wrapper {
    display: inline-block;
    width: 100%;
    position: relative;
    vertical-align: middle;
    margin: 0;
    padding: 0;
}
textarea {
    max-width: 100%;
    vertical-align: bottom;
    font-size: 14px;
}
.i-input {
    display: inline-block;
    width: 300px;
    line-height: 1.5;
    padding: 4px 7px;
    border: 1px solid #d7dde4;
    border-radius: 4px;
    color: #657180;
    background-color: #fff;
    background-image: none;
    position: relative;
    cursor: text;
}
.i-input:hover {
    border-color: #42b983;
}
.i-input:focus {
    border-color: #42b983;
    outline: 0;
}




